import React from 'react';

import Container from '@mui/joy/Container';
import Box, { BoxProps } from '@mui/joy/Box';
import Tooltip from '@mui/joy/Tooltip';
import IconButton from '@mui/joy/IconButton';
import GitHubIcon from '@mui/icons-material/GitHub';

interface AppHeaderProps extends BoxProps {
  title: string;
}

const headerHeight = 54;

const headerStyles = {
  p: 2,
  gap: 2,
  backgroundColor: 'white',

  display: 'flex',
  flexDirection: 'row',
  gridColumn: '1 / -1',

  alignItems: 'center',
  justifyContent: 'space-between',

  position: 'sticky',
  top: 0,
  zIndex: 40214,
};

export default (function Header(props) {
  const gitHubRepository = 'https://github.com/memset0/blog';

  return (
    <Box
      component="header"
      className="Header"
      {...props}
      sx={[
        headerStyles,
        {
          height: headerHeight,
          maxHeight: headerHeight,
        },
        {
          borderBottom: '1px solid',
          borderColor: 'divider',
        },
        ...(Array.isArray(props.sx) ? props.sx : [props.sx]),
      ]}
    >
      <Container
        sx={{
          position: 'relative',
        }}
      >
        <Box
          sx={{
            fontSize: '1.12rem',
            width: '100%',
            height: headerHeight,
            lineHeight: headerHeight + 'px',
            textAlign: 'center',
          }}
        >
          {props.title}
        </Box>
        <Tooltip
          title={'Github Repo'}
          enterDelay={300}
          sx={{
            position: 'absolute',
            top: (headerHeight - 40) / 2,
            right: 24,
          }}
        >
          <IconButton component="a" color="primary" href={gitHubRepository} target="_blank" data-ga-event-category="header" data-ga-event-action="github">
            <GitHubIcon fontSize="small" />
          </IconButton>
        </Tooltip>
      </Container>
    </Box>
  );
} as React.FC<AppHeaderProps>);
